

/*Generales*/

*{
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body{
  font-family: verdana;
  width: 80%;
  margin: 0 auto;
}
li{
  list-style: none;
}
a{
  text-decoration: none;
}
.clearfix::after{
  content: '';
  display: block;
  clear: both;
}
/*Header*/
.logo{
  text-align: center;
  font-size: 1.8em;
}
.contenedor{
  text-align: center;
}
.contenedor #fecha{
  float: left;
  margin-top:35px;
  display: inline-block;
}
.contenedor #nombre{
  float: right;
  display: inline-block;
  margin-top: 0px;

  color: #218DEF;
}
.contenedor .nav-principal{

  width: 25%;


  display: inline-block;
  margin-right:100px;
}
.contenedor .nav-principal nav {
  text-align: center;


}
.contenedor .nav-principal nav ul{

  margin-right: 40px;
}

.contenedor .nav-principal li{

  display: inline-block;
  margin: 10px 20px;

}
.pres{
  background-color: #4163B8;
  height: 500px;
  padding: 60px;
}
.pres #texto-pres{
  color: white;
  float: left;
  width:35%;
  padding:50px;
  font-size: 1.5em;
}
.pres #autor{
  font-size: 0.8em;
}
.main #Titulares h2{
  float: left;
}
.main #Titulares p{
  float: right;
  font-weight: bold;
}
.main .main-cuerpo{
  width: 70%;
  float:left;
}
.main .aside{
  width: 30%;
  float:right;
}
.main .main-cuerpo .categoria{
  width: 45%;
  float:left;
  text-align: left;
  padding: 20px;
}
.main .main-cuerpo .categoria .imagen{
  width:100%;
  background-color: #492277;
  height: 200px;
}
.main .aside #top-noticias{
  padding: 10px;
  text-align: left;
  border: 1px solid black;
}
.main .aside .noticiaTop{
  margin-bottom: 60px;
}
.main .aside .imagen{
  float: left;
  width:20%;
  height: 70px;
  background-color:#115299;
  margin-right: 20px;
}
.main .aside h5{
}

/**Animaciones**/
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}
.example-enter {
  opacity: 0.01;
}

.example-enter.example-enter-active {
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  opacity: 1;
}

.example-leave.example-leave-active {
  opacity: 0.01;
  -webkit-transition: opacity 300ms ease-in;
  -o-transition: opacity 300ms ease-in;
  transition: opacity 300ms ease-in;
}



